<template>
    <div>
      <div style="margin-bottom:20px">
        <el-input style="width: 240px" placeholder="请输入文物名称" v-model="params.name"></el-input>
        <el-input style="width: 240px ;margin-left: 5px;" placeholder="请输入文物编码" v-model="params.noNumber"></el-input>

        <el-button style="margin-left: 5px" type="primary" @click="load()">
          <i class="el-icon-search"></i> 搜索
        </el-button>
        <el-button style="margin-left: 5px" type="warning" @click="reset()">
          <i class="el-icon-refresh"></i> 重置
        </el-button>
      </div>
  
  
      <el-table :data="tableData" stripe row-key="id" default-expand-all>
              <el-table-column prop="id" label="序号"></el-table-column>
              <el-table-column prop="name" label="文物名称"></el-table-column>
              <el-table-column prop="description" label="描述" width="200"></el-table-column>
              <el-table-column prop="dynasty" label="朝代"></el-table-column>
              <el-table-column prop="address" label="来源"></el-table-column>
              <el-table-column prop="category" label="分类"></el-table-column>
              <el-table-column prop="noNumber" label="编码"></el-table-column>
              <el-table-column prop="state" label="状态"></el-table-column>
              <el-table-column prop="photo" label="照片">
                <template v-slot="scope">
                  <el-image :src="scope.row.photo" :preview-src-list="[scope.row.photo]"></el-image>
                </template>
              </el-table-column>
              <el-table-column prop="createtime" label="创建时间"></el-table-column>
              <el-table-column prop="updatetime" label="更新时间"></el-table-column>

              <el-table-column label="操作" width="280">
                <template v-slot="scope">

                    <el-button type="primary" @click="$router.push('/editAncient?id='+scope.row.id)">编辑</el-button>
                    <el-popconfirm
                        style="margin-left : 5px"
                        title="您确定删除这行数据吗?"
                        @confirm="del(scope.row.id)"
                    >
                        <el-button type="danger" slot="reference" >删除</el-button>
                    </el-popconfirm>
                </template>
              </el-table-column>
      </el-table>
  
      <!--分页-->
      <div style="margin-top:20px">
        <el-pagination
            background
            :current-page="params.pageNum"
            :page-size="params.pageSize"
            layout="prev,pager,next"
            @current-change="handleCurrentChange"
            :total="total">
      </el-pagination>
      </div>
  
    </div>
</template>
  
<script>
import request from '@/utils/request'
import Cookies from 'js-cookie'

export default {
  name: 'AncientList',
  data(){
        return {
          admin:Cookies.get('admin')?JSON.parse(Cookies.get('admin')):{},
            tableData:[],
            total:0,
            params:{
              pageNum:1,
              pageSize:10,
              name:'',
              noNumber:''
            },
            rules:{
              name:[
                {required:true,message:"请输入文物名称",trigger:'blur'},
              ]
            },
        }
    },
    created(){
        this.load()
    },
    methods:{
        load(){
            //fetch('http://localhost:8080/item/list').then(res => res.json()).then(res =>{
            //    console.log(res)
            //    this.tableData = res
            //})

            request.get('/ancient/page',{
              params:this.params
            }).then(res => {
              if(res.code === '200'){
                this.tableData = res.data.list
                this.total = res.data.total
              }

            })
        },
        reset(){
          this.params ={
            pageNum:1,
            pageSize:10,
            name:'',
            noNumber:''
          }
          this.load()
        },

        handleCurrentChange(pageNum){
          this.params.pageNum = pageNum;
          this.load()
        },
        del(id){
            request.delete("/ancient/delete/" + id).then(res => {
                if(res.code === '200'){
                    this.$notify.success('删除成功')
                    this.load()
                }else{
                    this.$notify.error(res.msg)
                }
            })
        }
      }
}
</script>

<style scoped>

</style>
  